
.home{
    min-height: 100%;
}

.helloText{
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    white-space: nowrap;
    color: white;
}


.helloIm{
    font-size: 2rem;
    font-weight: 400;
    white-space: nowrap;
    color: white;
    
}

.myName{
    font-size: 4rem;
    font-weight: 400;
    text-shadow: 2px 2px 3px rgb(27, 29, 31);
    
}

.softDev{
    font-size: 1.8rem;
    font-weight: 100;
    white-space: nowrap;
    color: rgb(221, 221, 221);
    text-shadow: 2px 2px 3px rgb(27, 29, 31);
}

.softDevWrap{
    font-size: 1.8rem;
    font-weight: 100;
}


.homeLinksDiv {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: space-between;
    margin: 3rem auto;
    padding-bottom: 3rem;
    border-bottom: 0px solid rgb(100, 100, 100);
}

.homeLink{
    display: flex;
    justify-content: space-between;
    overflow-x: hidden;


}

.homeLinkTextDivLeft{
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.homeLinkTextDivRight{
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
}

.homeLinkTextRight{
    display: flex;
    font-size: 3rem;
    justify-content: right;
    text-align: right;
    color: white;
    font-weight: 100;
    text-shadow: 2px 2px 3px rgb(27, 29, 31);
}


.homeLinkTextLeft{
    display: flex;
    font-size: 3rem;
    justify-content: left;
    color: white;
    font-weight: 100;
    text-shadow: 2px 2px 3px rgb(27, 29, 31);
}

.homePhotoDivLeft{
    display: flex;
    width: 50%;
    align-items: flex-end;
    justify-content: right;
    z-index: -1;
}

.homePhotoDivRight{
    display: flex;
    width: 50%;
    align-items: flex-start;
    z-index: -1;
}

.homePhoto {
    object-fit: fill;
    width: 40rem;
    min-width: 20rem;
}

.pongPhoto {
    object-fit: fill;
    width: 30rem;
    min-width: 20rem;
    border: 2px solid gray;
}

.portraitPhoto {
    object-fit: fill;
    width: 40rem;
    min-width: 20rem;

}



@media screen and (max-width:1050px) {
    .helloText{white-space: break-spaces;}
}


